<template>
  <Layout class="case-detail-page">
    <Row class="tool-bar">
      <div class="left">
        <a href="javascript: void(0)" style="margin-bottom: 0.5rem;" @click="$router.go(-1)">
          <icon type="ios-arrow-back" />
          返回
        </a>
      </div>
      <div class="right">
        <!-- <Button type="error" size="small" @click.native="deleteCase">删除</Button> -->
        <Button type="primary" size="small" @click.native="saveCase">保存</Button>
      </div>
    </Row>

    <Form class="case-detail-foem" :label-width="84" :model="caseData">
      <Row type="flex" class="form-row">
        <FormItem class="form-item" label="分类:">
          <professionalSelector :professionalId="caseData.professionId" @on-select="caseData.professionId = $event.id; caseData.tagInfo = []"/>
        </FormItem>
        <FormItem class="form-item" label="标题:">
          <Input v-model="caseData.title"/>
        </FormItem>
        <FormItem class="form-item" label="标签:">
          <tagSelector v-model="caseData.tagInfo" :professionalId="caseData.professionId"/>
        </FormItem>
      </Row>
      <Row type="flex" class="form-row">
        <FormItem class="form-item" label="案列网址:">
          <Input v-model="caseData.caseUrl"/>
        </FormItem>
        <FormItem class="form-item" label="案列编号:">
          <Input v-model="caseData.caseNumber"/>
        </FormItem>
        <FormItem class="form-item" label="法院名称:">
          <Input v-model="caseData.courtName"/>
        </FormItem>
      </Row>
      <Row type="flex" class="form-row">
        <FormItem class="form-item" label="案由:">
          <Input v-model="caseData.caseReason"/>
        </FormItem>
        <FormItem class="form-item" label="判决书类型:">
          <RadioGroup v-model="caseData.judgmentType">
            <Radio  :label="1">无</Radio>
            <Radio :label="2">判决书</Radio>
            <Radio :label="3">裁定书</Radio>
            <Radio :label="4">民事调解书</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem class="form-item" label="第几审:">
          <RadioGroup v-model="caseData.number">
            <Radio :label="1">无</Radio>
            <Radio :label="2">一审</Radio>
            <Radio :label="3">二审</Radio>
            <Radio :label="4">再审</Radio>
          </RadioGroup>
        </FormItem>
      </Row>
      <Row type="flex" class="form-row">
        <FormItem class="form-item" label="审判长:">
          <Input v-model="caseData.chiefJudge"/>
        </FormItem>
        <FormItem class="form-item" label="所在区域:">
          <areaSelector :value="caseData.area" @on-select="selectArea" />
        </FormItem>
        <FormItem label="判决日期:">
          <DatePicker type="date" placeholder="请选择日期" v-model="caseData.judgmentDate" />
        </FormItem>
      </Row>
      <Row style="z-index: 1">
        <FormItem label="内容:">
          <mavon-editor
            class="md-editor"
            v-model="caseData.content"
            :scrollStyle="true"
            placeholder="请输入内容"
            @fullScreen="toggleFullScreen"
            @change="contentChange"
            />
        </FormItem>
      </Row>
    </Form>
  </Layout>
</template>

<script>
import professionalSelector from '@/components/professional-selector'
import tagSelector from '@/components/tag-selector'
import areaSelector from '@/components/area-selector'

import { saveCase, getCaseDetail } from '@/api/case'

export default {
  name: 'case-detail',
  components: {
    professionalSelector,
    tagSelector,
    areaSelector
  },
  data () {
    return {
      verdict: '',
      trial: '',
      contentValue: '',
      caseData: {
        area: 0,
        caseNumber: '',
        caseReason: '',
        caseUrl: '',
        chiefJudge: '',
        content: '',
        courtName: '',
        judgmentDate: '',
        judgmentType: 0,
        number: 0,
        sort: 0,
        title: '',
        professionId: 0,
        tagInfo: []
      }
    }
  },
  created () {
    if (this.$route.params.type === 'detail') {
      getCaseDetail({ id: this.$route.params.id }).then(({ data, response }) => {
        if (response.success) {
          data.tagInfo = data.tagInfo ? data.tagInfo : []
          data.professionId = data.professionId ? data.professionId : 0
          data.judgmentDate = new Date(data.judgmentDate)
          this.caseData = data
        }
      })
    }
  },
  methods: {
    toggleFullScreen (params) {
      this.$store.commit('layout/toggleFullScreen', false)
    },
    saveCase () {
      saveCase(this.caseData).then(({ data, response }) => {
        if (response.success) {
          this.$Message.success('保存成功！')
          this.$router.go(-1)
        }
      })
    },
    contentChange (value, html) {
      this.caseData.content = value
    },
    selectArea (data) {
      this.caseData.area = data.id
    }
  }
}
</script>

<style scoped lang="stylus">
.case-detail-page
  background none
  height 100%
  overflow auto
  padding 0.5rem

  .form-row
    flex-wrap nowrap
    .form-item
      flex 1 1 auto

  .tool-bar
    display flex
    flex-wrap nowrap
    margin-bottom: 0.5rem;
    flex: 0 0 auto;
    .left
      flex 1 1 auto
    .right
      flex 1 0 auto
      text-align right
</style>
