<template>
  <Cascader
    :data="areaData"
    change-on-select
    v-model="selected"
    @on-change="select" />
</template>

<script>
import { getAreaList } from '@/api/area'

export default {
  name: 'area-selector',
  props: {
    value: {
      type: Number | String,
      required: true
    }
  },
  data () {
    return {
      selected: [],
      areaData: [],
      areaMap: new Map()
    }
  },
  created () {
    this.getAreaList()
  },
  watch: {
    value () {
      this.makeSelected()
    }
  },
  computed: {
  },
  methods: {
    getAreaList () {
      getAreaList({
        allDescendant: true
      }).then(resp => {
        if (resp.response.success) {
          this.areaData = this.makeData(resp.data)

          this.makeSelected()
        } else {
          this.$Message.error(resp.response.msg)
        }
      }).catch(err => {
        console.error(err)
      })
    },
    select (value, data) {
      this.$emit('on-select', data[data.length - 1])
    },
    makeData (list) {
      const rootLevel = []
      const subLevel = []
      // 生成map，分拣出根级数据和子集
      for (let item of list) {
        item.value = item.id
        item.label = item.name

        this.areaMap.set(item.id, item)
        if (!item.parentId) {
          rootLevel.push(item)
        } else {
          subLevel.push(item)
        }
      }

      // 将子集数据放入父级节点的chidren中
      for (let item of subLevel) {
        const parentNode = this.areaMap.get(item.parentId)
        if (!parentNode) {
          return
        }
        if (parentNode.children) {
          parentNode.children.push(item)
        } else {
          parentNode.children = [ item ]
        }
      }

      return rootLevel
    },
    makeSelected () {
      if (!this.value || !this.areaMap.size) {
        return
      }
      this.selected = [ this.value ]
      const lastNode = this.areaMap.get(this.value)
      let currentNode = lastNode ? this.areaMap.get(lastNode.parentId) : null
      while (currentNode) {
        this.selected.splice(0, 0, currentNode.id)
        currentNode = this.areaMap.get(currentNode.parentId)
      }
    }
  }
}
</script>

